import PlayOut from "../pages/Home/PlayOut";

const routes = [
    {
        path: '/',
        redirect: '/login'
    },
    {
        path: '/home',
        element: <PlayOut/>,
        // 根元素使用 element 的方式导入，避免路由导航的时候刷新根元素
        children: [
            {
                path: 'teacher',
                component: () => import('../pages/Index/Teacher'),
                // component 的方式导入路由懒加载
                meta: {
                    title: '教师首页',
                    Auth: 'teacher'
                    // 配合 App.js 中的路由拦截模块进行校验
                }
            },
        ]
    },
    {
        path: '/login',
        component: () => import('../pages/User/Login'),
        meta: {
            title: '登录',
            Auth: 'public'
        }
    },
    {
        path: '/register',
        component: () => import('../pages/User/Register'),
        meta: {
            title: '注册',
            Auth: 'public'
        }
    },
    {
        path: '/Spin',
        component: () => import('../component/loading/Spin'),
        meta: {
            Auth: 'public'
        }
    },
    {
        path: '/403',
        component: () => import('../pages/Error/403'),
        meta: {
            title: '403',
            Auth: 'public'
        }
    }, {
        path: '/404',
        component: () => import('../pages/Error/404'),
        meta: {
            title: '404',
            Auth: 'public'
        }
    },
    {
        path: '/500',
        component: () => import('../pages/Error/500'),
        meta: {
            title: '500',
            Auth: 'public'
        }
    }, {
        path: '*',
        redirect: '/404',
    }
]

export default routes